// Login.js:
import React, { useState } from 'react';
import { Form, Button, Container, Row, Col } from 'react-bootstrap';
import { withLDConsumer } from "launchdarkly-react-client-sdk";
import QrCode from './Qrcode';
import { ChatDots } from 'react-bootstrap-icons';
import Modal from 'react-bootstrap/Modal';

function Example() {
  const [show, setShow] = useState(false);

  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

  return (
    <>
      <Button className='chatButton' margin={2} variant="flat" onClick={handleShow}>
      <ChatDots></ChatDots>
      </Button>

      <Modal show={show} onHide={handleClose}>
        <Modal.Header closeButton>
          <Modal.Title>Modal heading</Modal.Title>
        </Modal.Header>
        <Modal.Body>Woohoo, you are reading this text in a modal!</Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={handleClose}>
            Close
          </Button>
          <Button variant="primary" onClick={handleClose}>
            Save Changes
          </Button>
        </Modal.Footer>
      </Modal>
    </>
  );
}

function Login({flags, variant}) {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [name, setName] = useState('');
  const [dob, setDob] = useState('');
  var custLogo = "";

  console.log(flags.custLogo)
  const qrCodeValue = `${window.location.href}#page/${variant}`;
  custLogo = flags.custLogo

  const handleSubmit = (event) => {
    event.preventDefault();
    console.log('Login attempted with:', { email, password });
    // Here you would typically send a request to your server
  };
  
  return (
     
    <Container className="loginForm" style={{ "justifyContent":"center" }}>
      <Row className="justify-content-lg-center ">
        <Col xs={12} md={10}>
        <img src={custLogo} className="logoImage" alt="Logo"></img>
          <Form onSubmit={handleSubmit} style={{ "justifyContent":"center" }}>
          <Form.Group className="mb-auto" controlId="formBasicName">
              <Form.Label>Name</Form.Label>
              <Form.Control
                type="name"
                placeholder="Sam Sample"
                value={name}
                onChange={(e) => setName(e.target.value)}
              />
            </Form.Group>
            <Form.Group className="mb-auto" controlId="formBasicEmail">
              <Form.Label>Email address</Form.Label>
              <Form.Control
                type="email"
                placeholder="Enter email"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
              />
            </Form.Group>

            <Form.Group className="mb-auto" controlId="formBasicPassword">
              <Form.Label>Password</Form.Label>
              <Form.Control
                type="password"
                placeholder="Password"
                value={password}
                onChange={(e) => setPassword(e.target.value)}
              />
            </Form.Group>

            <Form.Group className="mb-auto" controlId="formBasicDOB">
              <Form.Label>Date Of Birth</Form.Label>
              <Form.Control
                type="date"
                placeholder="30/03/1980"
                value={dob}
                onChange={(e) => setDob(e.target.value)}
              />
            </Form.Group>
          
            <>  
             <style type="text/css">
    {`
.btn-flat {
  background-color: #183F49;
  color: #F7CC45;
  font-weight: bolder;
}


`}
</style>
            <Button margin={2} variant="flat" type="submit" className="w-90">
              Submit
            </Button>
            </>
          </Form>
        </Col>
      </Row>

      <QrCode value={qrCodeValue}/>
      <div className="chatBox">
      <Example> </Example>
  
      </div>
      <div
            style={{
                textAlign: "center",
                display: "block",
                padding: 30,
                margin: "auto",
            }}
        >
            
            
        </div>
   
    </Container>
    
  );
  
}
export default withLDConsumer()(Login);

